class SuperSkill extends React.Component<ISkill> {
  render() {
    return null;
  }
}


class BlankSkill extends React.Component<ISkill> {
  render() {
    return null;
  }
}


interface SkillData {
  occupation?: number;
  interest?: number;
  grow?: number;
}


class Skill extends React.Component<ISkill, SkillData> {
  constructor(props: ISkill) {
    super(props);
    this.state = {}
  }

  render() {
    if (this.props.contains !== undefined) {
      return <SuperSkill {...this.props}/>
    }
    if (this.props.tag.some((tag) => tag === "blank")) {
      return <BlankSkill {...this.props}/>
    }
    const initial = this.props.initial;
    const occupation = this.state.occupation;
    const interest = this.state.interest;
    const grow = this.state.grow;
    let sum = 0;
    if (typeof initial === "number") sum += initial;
    if (occupation !== undefined) sum += occupation;
    if (interest !== undefined) sum += interest;
    if (grow !== undefined) sum += grow;
    const is_mythos = this.props.en === "Cthulhu Mythos";
    const change = sum === initial;
    return <tr className={change ? "skill-row changed" : "skill-row"}>
      <td>
        <div className="field"><input type="checkbox" className="checkbox" disabled={is_mythos}/></div>
      </td>
      <td className="has-text-centered">
        <ruby>{this.props.label}
          <rt>{this.props.en}</rt>
        </ruby>
      </td>
      <td>{initial}</td>
      <td>
        <div className="field">
          <NumberInput max={100} value={occupation} className="input is-small"
                       editable={!is_mythos} style={{maxWidth: "5em"}}
                       onChange={(n) => this.setState({occupation: n})}/>
        </div>
      </td>
      <td>
        <div className="field">
          <NumberInput max={100} value={interest} className="input is-small"
                       editable={!is_mythos} style={{maxWidth: "5em"}}
                       onChange={(n) => this.setState({interest: n})}/>
        </div>
      </td>
      <td>
        <div className="field">
          <NumberInput max={100} value={grow} className="input is-small"
                       style={{maxWidth: "5em"}}
                       onChange={(n) => this.setState({grow: n})}/>
        </div>
      </td>
      <td className={sum >= 100 ? "skill-out-range" : undefined}>{sum}</td>
    </tr>;
  }
}


interface Props {
  attributes: Map<string, number>;
}


interface State {
  filter: string;
}


export class Skills extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {filter: ""}
  }

  render() {
    const filter = this.state.filter;
    const rows = skills
      .filter((row) => row.en.toLowerCase().includes(filter) || row.label.includes(filter))
      .map((row) => {
        const props = {...row};
        if (props.initial == "edu") props.initial = this.props.attributes.get("edu", 0);
        else if (row.initial == "dex/2") props.initial = div(this.props.attributes.get("dex", 0), 2);
        return <Skill key={row.en} {...props}/>;
      });
    const skill_filter = <div className="field">
      <label className="label" htmlFor="skill-filter"></label>
      <div className="control">
        <input id="skill-filter" className="input" value={filter} placeholder=""
             onChange={(e) => this.setState({filter: e.currentTarget.value})}/>
      </div>
    </div>;
    return (
      <div className="container">
        <h2 className="title is-4"></h2>
        {skill_filter}
        <table className="table is-striped is-fullwidth is-narrow">
          <tbody>
          <tr>
            <th/>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          {rows}
          </tbody>
        </table>
      </div>
    )
  }
}
